/** @jsxImportSource @emotion/react */
import describecompanyimg from '../../../images/ComDesc.png';
import styles from "./CompanyDescription.module.css";

export default function CompanyDescription({t}) {
    const isMobile = window.innerWidth < 850;
    const tt= isMobile ? t("landing.free") : t("landing.joinNow");

    return (
        <div className={styles.container}>
            <div className={styles.body}>
                <div className={styles.sectionOne}>
                    <h1 className={styles.heading}>
                        {t("landing.heading")}
                    </h1>

                <div className={styles.sectionTwo}>
                <div className={styles.twoContainer} >
                    <p className={styles.subHeading} >
                        {t("landing.subheading")}
                    </p>
                    <span>
                    <button className={styles.signin}>{t("landing.signin")}</button>
                    <button className={styles.joinNow}>{tt}</button>
                    </span>
                </div>
                <div className={styles.sectionThree}>
                    <img src={describecompanyimg} className={styles.study} alt="Study" />
                </div>
                </div>
                </div>
            </div>
        </div>
    );
}