/** @jsxImportSource @emotion/react */
import React, { Component } from 'react'
import styles from "./CoursesPage.module.css";
import SearchBar from "material-ui-search-bar";
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import { AudioCard, VideoCard,SoundButton } from 'material-ui-player'
import video from "../../images/video.mp4";
import Carousel from 'react-elastic-carousel'
import pc1 from "../../images/pc1.png";
import pc2 from "../../images/pc2.png";
import pc3 from "../../images/pc3.png";

const categories = [
    { label: 'Maths' },
    { label: 'Science'},];

export default function OurPartners() {
    const isMobile = window.innerWidth < 850;
    const showItems = isMobile ? 3 : 3;
    const filwidth=isMobile?"90px":"190px";
    const [showPlay, setShowPlay] = React.useState(true);
    const videoRef = React.useRef(null);

    const toggleVideoState = () => {
        setShowPlay(!showPlay);
        if (!videoRef.current) return;

        if (showPlay) {
            videoRef.current.play();
        } else {
            videoRef.current.pause();
            videoRef.current.currentTime = 0;
        }
    };
    return (
        <div className={styles.coursespage}>
         {isMobile&&(
         <div className={styles.headund}>
         <h1 className={styles.courseshead}>
                 COURSES
          </h1>
          <div className={styles.und} />
         </div>)}
         <div className={styles.mainsection}>  
             <div className={styles.welcomesection}>
                <h1 className={styles.welcome}>Welcome to Ariceum</h1>
                 <p className={styles.para}>
                 Lorem ipsum Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod Lorem ipsum dolor sit amet,  consectetur adipiscing elit, sed do eiusmod Lorem ipsum dolor sit amet, consectetur 
                 </p>
             </div>  
             <div className={styles.sfilter}>
             
             <SearchBar className={styles.search} />
             
             <Autocomplete
                className={styles.category}
                disablePortal
                id="combo-box-demo"
                options={categories}
                sx={{ width: filwidth }}
                renderInput={(params) => <TextField {...params} label="Categories" />}
            />
            <Autocomplete
                className={styles.category}
                disablePortal
                id="combo-box-demo"
                options={categories}
                sx={{ width: filwidth }}
                renderInput={(params) => <TextField {...params} label="Topics" />}
            />
            
             </div>
            
             <div className={styles.video}>
                <video ref={videoRef} className={styles.player} controls={showPlay} autoPlay={false} preload="auto" onEnded={toggleVideoState}>
                    <source src={video} type="video/mp4" />
                </video>
             </div>
            <div className={styles.pcourses}>
            <h1 className={styles.popular}>Popular Courses</h1>
            <Carousel itemsToShow={showItems} enableAutoPlay={true} autoPlaySpeed={1000} showArrows={false} className={styles.carousels}>
             <img src={pc1} className={styles.img}/>
             <img src={pc2} className={styles.img}/>
             <img src={pc3} className={styles.img}/>
             <img src={pc1} className={styles.img}/>
            </Carousel>
            </div>

            <div className={styles.pcourses}>
            <h1 className={styles.popular}>Recommended Courses</h1>
            <Carousel itemsToShow={showItems} enableAutoPlay={true} autoPlaySpeed={1000} showArrows={false} className={styles.carousels}>
             <img src={pc1} className={styles.img}/>
             <img src={pc2} className={styles.img}/>
             <img src={pc3} className={styles.img}/>
             <img src={pc1} className={styles.img}/>
            </Carousel>
            </div>

            <div className={styles.rvsection}>
            <h1 className={styles.videoheading}>Recommended Videos</h1>
            <div className={styles.rv}>
            <div className={styles.video2}>
                <video ref={videoRef} className={styles.player2} controls={showPlay} autoPlay={false} preload="auto" onEnded={toggleVideoState}>
                    <source src={video} type="video/mp4" />
                </video>
             </div>
             <div className={styles.video2}>
                <video ref={videoRef} className={styles.player2} controls={showPlay} autoPlay={false} preload="auto" onEnded={toggleVideoState}>
                    <source src={video} type="video/mp4" />
                </video>
             </div>

             </div>
            </div>

         </div>

         </div>   
    );
}