/** @jsxImportSource @emotion/react */
import React from 'react'
import plus from '../../../images/Splus.png'
import minus from '../../../images/minus.png'
import styles from './FAQ.module.css'


const Faq = ({ t }) => {
  const [opened, setOpened] = React.useState({})

  const Data = [
  {
    id: "mission",
    title: t("landing.faq1"),
    content: t("landing.ans1"),
  },
  {
    id: "benefits",
    title: t("landing.faq2"),
    content: t("landing.ans2")
  },
  {
    id: "expect",
    title: t("landing.faq3"),
    content: t("landing.ans3")
  },
  {
    id: "track",
    title: t("landing.faq4"),
    content: t("landing.ans4")
  },
];

  return (
    <div className={styles.container}>
      <div className={styles.body}>
        <div className={styles.heading}>{t("landing.faq")}</div>
        <div className={styles.faqs} />

        { Data.length > 0 && (<div className={styles.faqContainer} >
          { Data.map(({ title, content, id }) => (
            <div key={id} className={styles.tabsContainer}>
              <div className={styles.tabs} role="button" tabIndex="0" aria-pressed={Boolean(opened[id])} onKeyPress={(e) => e.code === 'Enter' ? setOpened({ ...opened, [id]: !opened[id]}): null} onClick={() => setOpened({ ...opened, [id]: !opened[id]})}>
                <h3 css={{ color: opened[id] ? "white" : "white" }} className={styles.questions}>{title}</h3>
                <p></p>
                <img src={opened[id]?minus:plus} className={opened[id] ? styles.plus : styles.minus } alt="" />
              </div>
              { opened[id] && <p className={styles.answers}>{content}</p> }
            </div>
            ))
          }
        </div>) 
        }
      </div>
    </div>
  )
}

export default Faq
